import { Parser } from "html-to-react";
import { useLayoutEffect } from "react";
import { useLocation } from "react-router-dom";

export const htmlToReactParser = (html) => {
  const htmlToReactParser = new Parser();
  return htmlToReactParser.parse(html);
};

export const checkScrollBottom = () => {
  const innerHeight = window.innerHeight;
  const { scrollHeight } = document.body;
  const { scrollTop } = document.documentElement;
  if (innerHeight + scrollTop + 48 >= scrollHeight) {
    //  Wrapper  global style  margin: 48px auto scrollHeight  edge case: border, scrollbar, margin 
    return true;
  }
  return false;
};

export function ScrollToTop() {
  const { pathname } = useLocation();
  useLayoutEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);
  return null;
}

//  ( yyyy-MM-dd , )
export function isValidDate(dateString) {
  if (!/^\d{4}-\d{1,2}-\d{1,2}$/.test(dateString)) return false;
  const date = new Date(dateString + 'T00:00:00+0800') // 
  if (!date) return false
  return date <= new Date()
}